@charset "UTF-8";

@import "scss/variables";
@import "scss/typography";
@import "scss/forms";

.wooya-wrapper {
	.wooya-description {
		max-width: 600px;
		font-size: 15px;
		line-height: 19px;
		text-align: center;
		margin: 30px auto;
	}

	.wooya-version {
		position: absolute;
		top: 20px;
		right: 20px;
	}

	.me-main-content > button {
		margin-bottom: 50px;
	}

	.in-progress {
		min-height: 100px;
		background: url('#{$img-path}/loader.gif') no-repeat center center/100px;
	}
}
